@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
	 box-sizing: border-box;
}
html{
	height: 100%;
}
#root{
	height: 100%;
}
body {
	margin: 0px;
	padding: 0px;
	background-color: #ececec;
	height: 100%;
	font-family: "Open Sans", sans-serif;
}
/* scrollbar css */
::-webkit-scrollbar { /* WebKit (Chrome, Safari, etc.) */
	width: 0;
	height: 0;
}
/* slider css */

.slick-track{
	margin-left: 0 !important;
}
.slick-arrow {
	bottom:-3.5rem;
	cursor: pointer;
	position: absolute;
	border:1px solid #dfdfdf;
	height: 2.1rem;
	width: 2.1rem;
	display: flex;
	align-items: center;
	justify-content: center;
	color:#808080;
}
.slick-arrow.slick-disabled{
	cursor: not-allowed;
}
.slick-arrow:hover{
	background-color: #000;
	color:#fff;
}

.slick-arrow.slick-disabled{
	background-color: #aaa !important;
	color:#fff !important;
}
.slick-next{
	left:2.5rem;
}
.slick-prev{
	left:0rem ;
}

.slick-dots{
	display: flex !important;
	position: absolute;
	top:1.2rem;
	left:50%;
	transform: translateX(-50%);
	gap:2;
	font-size: 0px;
}
.slick-dots li.slick-active button{
	background: #2F3645;
}
.slick-dots li button{
	width: 20px;
	height:4px;
	border-radius: 1rem;
	margin-right:4px;
	background-color: #ccc;
}

.storieArrow .slick-arrow{
	top:50%;
	transform: translateY(-50%);
	bottom: auto;
	border-radius: 50%;
	height: 3rem;
	width:3rem;
	background-color: #fff;
	color:#000;
}
.storieArrow .slick-arrow:hover{
	color:#000;
}
.storieArrow .slick-prev{
	left:-3.8rem;
}

.storieArrow .slick-next{
	right:-3.8rem;
	left: auto;
}

@media(max-width:640px){
	.storieArrow .slick-arrow{
		height:2.1rem;
		width: 2.1rem;
		border-radius: 0px;
	}
	.storieArrow .slick-prev{
		left:0rem;
		z-index: 1;
	}
	.storieArrow .slick-next{
		right:0rem;
		left: auto;
		z-index: 1;
	}
}
